@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

body{
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  background-color: #E8E5E2;
}

h1, h2, h3, h4, h5{
  font-weight: 300;
}

img{
  width: 100%;
}

a , a:hover{
  color: inherit;
  text-decoration: none;
  
}

.btn {
  border-radius: unset;
}

.btn-dark{
  background-color: #003049;
  border: none;
}

