.button-login-signup {
  border-radius: 4px;
  background-color: #42AB9E;
  border: none;
  padding: 10px;
  width: 200px;
  transition: all 0.5s;
}

.button-login-signup:focus{
  outline: none;
}

.button-login-signup span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.button-login-signup span:after {
  content: '»';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button-login-signup:hover span {
  padding-right: 25px;
}

.button-login-signup:hover span:after {
  opacity: 1;
  right: 0;
}

